<template>
	<div class="aside">
		<ul>
			<li  v-for="item in asideInfo">
				<img :src="item.image" :alt="item.name">
				<p v-text='item.name'></p>
			</li>
		</ul>
	</div>
</template>
<style>
	.aside{
		width:11.5%;
		min-width: 120px;
		display: inline-block;
		vertical-align: top;
	}
	.aside ul{
		width: 100%
	}
	.aside li{
		display: block;
		width: 90%;
		height: 50px;
		margin: 0 auto;
		margin-bottom: 5px;
		background-color: #fff;
		padding: 15px 0;
	}
	.aside .choose{

	}
	.aside li img{
		width: 35px;
		height: 35px;
		display: block;
		margin: 0 auto;
	}
	.aside li p{
		text-align: center;
		margin-top: 5px;
		font-family: '微软雅黑';
		font-size: 14px;
	}
	.aside li:hover{
		transform: scale(1.2,1.2);
		box-shadow: 0px 5px 30px #405ca3;
		color: #405ca3;
		background:white url("../../../static/images/right.png") no-repeat right center;
		background-size: 20px 20px;
	}
</style>
<script>
export default {
  	data () {
   		return{
   			asideInfo:[
   				{name:'全部',image:'static/images/all.png'},
   				{name:'HTML',image:'static/images/HTML5.png'},
   				{name:'CSS',image:'static/images/css3.png'},
   				{name:'jQuery',image:'static/images/jquery.png'},
   				{name:'js',image:'static/images/js.png'},
   				{name:'Vue',image:'static/images/vue.png'},
   				{name:'nodejs',image:'static/images/nodejs.png'},
   				{name:'webpack',image:'static/images/webpack.png'},
				{name:'其他',image:'static/images/more.png'},
   			]
   		}
    }
}
</script>



